import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import ManagerInfo from '@/views/ManagerInfo.vue'
import Profile from '@/views/managerInfo/Profile.vue'
import Certificate from '@/views/managerInfo/Certificate.vue'
import CompteManager from '@/views/managerInfo/CompteManager.vue'
import JobsManager from '@/views/managerInfo/JobsManager.vue'
import NewsManager from '@/views/managerInfo/NewsManager.vue'
import RegisteManager from '@/views/managerInfo/RegisteManager.vue'
import Information from '@/views/managerInfo/Information.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home',
  },
  {
    path: '/home',
    component: Home,
  },
  {
    path: '/managerInfo',
    component: ManagerInfo,
    redirect: '/managerInfo/certificate',
    children: [
      { path: 'profile', component: Profile },
      { path: 'certificate', component: Certificate },
      { path: 'compteManager', component: CompteManager },
      { path: 'jobsManager', component: JobsManager },
      { path: 'newsManager', component: NewsManager },
      { path: 'registeManager', component: RegisteManager },
      { path: 'information', component: Information },
    ],
  },
]

const router = new VueRouter({
  routes,
})

router.beforeEach(function (to, from, next) {
  if (to.path !== '/home') {
    const token = localStorage.getItem('token')
    if (token) {
      next()
    } else {
      next('/home')
    }
  } else {
    next()
  }
})

export default router
